<h1>One of the benefits of Microsoft 365 Apps is that Microsoft provides new (and updated) features for Office apps, such as Excel and Word, on a regular basis. You can control how often the users in your organization get these new features by specifying the update channel.

In addition to new features, update channels provide, as needed, security and non-security updates on a regular basis, every month. Non-security updates provide fixes for known issues and provide stability or performance improvements for Office.</h1>
<br>
<h1>There are three channels:</h1>
<ul>
  <li>&#8226; <somenone>Monthly Channel</somenone> for Office 365 ProPlus</li>
  <li>&#8226; <somenone>Semi-Annual Channel</somenone> for Office 365 ProPlus</li>
  <li>&#8226; <somenone>Semi-Annual Channel (Targeted)</somenone> for Office 365 ProPlus</li>
</ul>
<br>
<h1>We recommend Current Channel, because it provides your users with the newest Office features as soon as they are ready. If you need additional predictability of when these new Office features are released each month, we recommend Monthly Enterprise Channel. In those cases where you have select devices that require extensive testing before receiving new features, we recommend Semi-Annual Enterprise Channel.</h1>
<br>
<h1><somenone>Monthly Channel </somenone>when you want to provide your users with the newest features of Office as soon as they are available and when you do not have many line-of-business applications, add-ins or macros that you need to test to determine if they work with an updated version Office.</h1>
<br>
<h1><somenone>Semi-Annual Channel </somenone>if your organization doesn't want to deploy the latest features of Office right away or you have a significant number of lines-of-business applications, add-ins or macros that you need to test with Office</h1>
<br>
<h1><somenone>Semi-Annual Channel (Targeted) </somenone>is for give pilot users and application compatibility testers in your organization a chance to work with the upcoming Semi-Annual Channel release.</h1>
<br>
